<template>
  <div>
    <el-table  :header-cell-style="{background:'#eef1f6',color:'#606266'}" :data="this.content"
      border  stripe style="width: 100%">
        <el-table-column label="" prop="schoolName"></el-table-column>
        <el-table-column label="" prop="classesName"></el-table-column>
        <el-table-column label="" prop="name"></el-table-column>
        <el-table-column label="" prop="gender"></el-table-column>
        <el-table-column label="" prop="lastTime"></el-table-column>
        <el-table-column label="" >
          <template slot-scope="scope" >
             <el-button type="primary"icon="el-icon-edit" circle @click="editStudent(scope.row.id)"></el-button>
             <el-button type="danger" icon="el-icon-delete" circle @click="deleteStudent(scope.row.id)"></el-button>
          </template>
         </el-table-column>
    </el-table>
    <el-pagination
        background
        :current-page="this.number"
        @current-change="handleCurrentChange"
        layout="prev, pager, next"
        :page-size ="this.size"
        :total="this.totalElements">
     </el-pagination>
     <!--  -->
       <el-dialog title="" :visible.sync="editStudentVisible" width="50%" :before-close="handleClose">
           <el-form :model="editStudentForm" :rules="editStudentRules" ref="studentFormRef" label-width="120px" >
           <el-form-item label="" prop="classesId">
              <el-select v-model="editStudentForm.classesId" placeholder=""  @change="handleClassChange">
                  <el-option
                    v-for="item in options"
                    :key="item.id"
                    :label="item.className"
                    :value="item.id">
                  </el-option>
                </el-select>
           </el-form-item>
           <el-form-item label="" prop="name">
               <el-input v-model="editStudentForm.name" clearable></el-input>
            </el-form-item>
            <el-form-item label="" prop="gender">
                 <el-radio v-model="editStudentForm.gender" size="medium" border :label="0"></el-radio>
                 <el-radio v-model="editStudentForm.gender" size="medium" border :label="1"></el-radio>
            </el-form-item>
            <el-form-item label ="" >
               <el-date-picker
                v-model="editStudentForm.birthday"
                type="date"
                @blur="dateChange"
                placeholder="">
               </el-date-picker>
            </el-form-item>
            <el-form-item label="()">
                 <el-input v-model="editStudentForm.height" clearable></el-input>
            </el-form-item>
            <el-form-item label="(KG)" >
                 <el-input v-model="editStudentForm.weight" clearable></el-input>
            </el-form-item>
            <el-form-item label="()" >
                 <el-input v-model="editStudentForm.sittingHeight" clearable></el-input>
            </el-form-item>
            <el-form-item label="" >
                     <el-radio v-model="editStudentForm.correct" size="medium" border  :label="1"></el-radio>
                     <el-radio v-model="editStudentForm.correct" size="medium" border :label="0"></el-radio>
            </el-form-item>
           </el-form>
           <span slot="footer" class="dialog-footer">
               <el-button @click="handleClose"> </el-button>
               <el-button type="primary" @click="sumitEditStudent" > </el-button>
           </span>
       </el-dialog>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'studentContent',
    created() {
      this.getInfo('', '');
      this.getOptions();
    },
    data() {
      var valiNumberPass1 = (rule, value, callback) => {//
       let reg = /^(([1-9]{1}\d*)|(0{1}))(\.\d{1,2})?$/g;
       if (value === '') {
           callback(new Error(''));
       } else if (!reg.test(value)) {
           callback(new Error(''));
       } else {
           callback();
       }
      }
      return {
       content: [],
       pageSize: 10,
       size: 5,
       total:0,
       totalElements: 0,
       number: 1,
       page: 1,
       classId: '',
       editStudentVisible: false,
       editStudentForm: {
           "sittingHeight":'',
           "correct": '',
           "gender":'',
           "height":'',
           "weight": '',
           "name":"",
           "birthday": '',
            classId: '',
            studentId: ''
       },
       editStudentRules: {
           name:  { required: true, message: '', trigger: 'blur' },

           correct:  { required: true, type: 'number',message: '', trigger: 'blur' },
           gender:  { required: true,type: 'number', message: '', trigger: 'blur' },
           height:  { required: true, validator: valiNumberPass1, message: '(m)', trigger: 'blur' },
           weight:  { required: true,validator: valiNumberPass1, message: '(kg)', trigger: 'blur' },
           sittingHeight: { required: true,validator: valiNumberPass1, message: '(cm)', trigger: 'blur' },
           classesId:{ required: true, message: '', trigger: 'change' },
           birthday: {type: 'date', required: true, message: '', trigger: 'change'}
       },
       options: []
      }
    },
    methods: {
      //
      getInfo(classId, name) {
        if(classId && !name) {  // 
          this.type = 'class';
          this.id = classId;
        }else if(classId && name) { // 
          this.type ="student";
          this.name = name;
          this.id = '';
        }else if (!this.classId && name) {
          this.type ="student";
          this.name = name;
          this.id = ''
        } else {   //
          this.type = '',
          this.id = ''
        }
        this.page = 1;
        this.getDataList()
      },
      // 
      getDataList() {
        let param = new FormData();
          param.append('type', this.type);
          param.append('id', this.id);
          param.append('page', this.page);
          param.append('name', this.name);
          axios({
            method: 'post',
            data: param,
            url: '/lightspace/school/getStudent'
          }).then(this.handleGetDataListSucc.bind(this)).catch((err) => {console.log(err)})
      },
      handleGetDataListSucc(res) {
        // console.log(res)
       if(res.data.status == 200) {
         res ? res= res.data.data: '';
         this.content = res.content;
         this.totalElements = res.totalElements;
         this.size = res.size;
         this.number = res.number + 1;
         if(this.content.length) {
           this.content.forEach((item) => {
            if(item.gender == 0) {
              item.gender = ''
            }else {
              item.gender = ''
            }
           })
         }
       }else {
         this.$message.error(res.data.msg)
         this.content = [];
       }
      },
      //
      handleCurrentChange(val) {
        this.page = val;
        this.getDataList();
      },
      //
       handleClose() {
       this.editStudentVisible = false;

      },
      handleClassChange(val) {
        this.classId = val
      },
      dateChange(val) {
      if(val) {
        this.startTime = val.toString().split(",")[0]
        this.endTime = val.toString().split(",")[1]
        this.birthday = val.toLocaleString().split(' ') [0];
      }
    },
      // 
      editStudent(id) {
          this.studentId =id;
          let param = new URLSearchParams();
          param.append('id', id);
          axios({
              method: 'post',
              url: '/lightspace/school/editStudent',
              data: param
          }).then(this.handleEditStuSucc.bind(this)).catch((err) => {console.log(err)})
      },

      handleEditStuSucc(res) {

        console.log(res)
        if(res.data.status === 10204) {
            this.$message.error(res.data.msg);
            this.$router.push('/login');
        } else if(res.data.status == 200) {
          this.editStudentForm = res.data.data;
          this.editStudentVisible = true;
        }
      },
      sumitEditStudent() {
        this.$refs.studentFormRef.validate((valid) => {
          if(!valid) return this.$message.error('');
          let param = new FormData();
          param.append('id', this.studentId);
          param.append('classId', this.classId);
          param.append('sittingHeight',this.editStudentForm.sittingHeight)
          param.append('correct', this.editStudentForm.correct)
          param.append('gender', this.editStudentForm.gender)
          param.append('height', this.editStudentForm.height)
          param.append('weight', this.editStudentForm.weight)
          param.append('name', this.editStudentForm.name)
          param.append('birthday', this.birthday);
          axios({
              method: 'post',
              url: '/lightspace/school/saveStudent',
              data: param
          }).then(this.handleEditSaveSucc.bind(this)).catch((err) => {console.log(err)})
          })
      },
      handleEditSaveSucc(res) {
        console.log(res)
        if(res.data.status === 10204) {
            this.$message.error(res.data.msg);
            this.$router.push('/login');
        } else if(res.data.status == 200) {
          this.editStudentVisible = false;
          this.$message.success('');
          this.getDataList()
      }
     },
     //
      async deleteStudent(id) {
        const confirmResult = await this.$confirm(' ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
        }).catch(err => err)
        if(confirmResult !== 'confirm') {
            return this.$message.info('')
        }
        let param = new FormData();
        param.append('id', id)
        axios({
            method: 'post',
            url: '/lightspace/school/deleteStudent',
            data: param
        }).then(this.handleDeleteClssSucc.bind(this))
        .catch((err) => {
          console.log(err)
        })
      },
      handleDeleteClssSucc(res) {
        if(res.data.status == 200) {
          this.$message.success('')
          this.getDataList();
        }
      },
      getOptions() {
        let param = new FormData();
        axios({
            method: 'post',
            url: '/lightspace/school/getAllClass'
        }).then(this.handleGetOptionsSucc.bind(this)).catch((err) => {
           console.log(err)
         })
      },
      handleGetOptionsSucc(res) {
        //console.log(res)
        if(res.data.status == 200) {
          this.options = res.data.data
        }
      },
  }
}
</script>

<style>
</style>



